
<!--账户信息，退出登录页面-->

<template>
    <div id="app">
        <div>
            <header id="head_top">
                <section class="head_goback" @click="goMy">
                    <i class="el-icon-arrow-left"></i>
                </section>
                <section class="title_head ellipsis">
                <span class="title_text">
                    账户信息
                </span>
                </section>
            </header>
                <section class="profile-info">
                    <section class="headportrait" >
                        <input type="file" class="profileinfopanel-upload">
                        <h2>头像</h2>
                        <div class="headportrait-div">
                            <img :src="'//elm.cangdu.org/img/'+user.avatar" alt="" class="headportrait-div-top">
                            <span class="headportrait-div-bottom">
                                <i class="el-icon-arrow-right"></i>
                            </span>
                        </div>
                    </section>
                    <!--//用户名-->
                    <router-link to="">
                        <section class="headportraitwo headportrait">
                            <h2>用户名</h2>
                            <div class="headportrait-div">
                                <p>{{user.username}}</p>
                                <span class="headportrait-div-bottom">
                                    <i class="el-icon-arrow-right"></i>
                                </span>
                            </div>
                        </section>
                    </router-link>
                    <!--//收获地址-->
                    <router-link to="">
                        <section class="headportraithree headportraitwo headportrait" >
                            <h2>收获地址</h2>
                            <div class="headportrait-div">
                                <span class="headportrait-div-bottom">
                                    <i class="el-icon-arrow-right"></i>
                                </span>
                            </div>
                        </section>
                    </router-link>
                    <!--账号绑定-->
                    <section class="bind-phone">
                        账号绑定
                    </section>
                    <!--手机-->
                        <section class="headportraithree  headportraitwo headportrait blue" @click="phone">
                            <h2>
                                <i class="el-icon-mobile"></i>
                                手机
                            </h2>
                            <div class="headportrait-div">
                                <p></p>
                                <span class="headportrait-div-bottom">
                                    <i class="el-icon-arrow-right"></i>
                                </span>
                            </div>
                        </section>
                    <!--安全设置-->
                    <section class="bind-phone">
                        安全设置
                    </section>
                    <!--登录密码-->
                    <router-link to="/forget">
                        <section class="headportraithree  headportraitwo headportrait">
                            <h2>
                               登录密码
                            </h2>
                            <div class="headportrait-div">
                                <p>修改</p>
                                <span class="headportrait-div-bottom">
                                    <i class="el-icon-arrow-right"></i>
                                </span>
                            </div>
                        </section>
                    </router-link>
                    <!--退出登录按钮-->
                    <section class="exitlogin" @click="logOut">
                        退出登录
                    </section>
                    <!--退出登录方框-->
                    <section class="coverpart" v-if="isShow">
                       <section class="cover-background"></section>
                        <section class="cover-animate  cover-content">
                            <div class="sa-icon">
                                <span class="sa-body"></span>
                                <span class="sa-dot"></span>
                            </div>
                            <h2>是否退出登录</h2>
                            <div class="sa-botton">
                                <button class="waiting" @click="await">再等等</button>
                                <div class="" style="display: inline-block;">
                                    <button class="quitlogin" @click="quit">退出登录</button>
                                </div>
                            </div>
                        </section>
                    </section>
                    <!--点击手机弹框-->
                    <section class="tip_text_container" v-if="phoneIsShow">
                        <div class="tip_icon">
                            <span></span>
                            <span></span>
                        </div>
                        <p class="tip_text">请在手机APP中设置</p>
                        <div class="confrim" @click="phoneClose">确认</div>
                    </section>
                </section>

        </div>
    </div>

</template>

<script>
    export default {
        name: "Info",
        data(){
            return{
                user:"",
                isShow:false,
                phoneIsShow:false,
            }
        },
        created(){
            this.axios.get("v1/user").then(res=>{
                console.log(res)
                this.user = res;
            }).catch(err=>{})
        },
        methods:{
            //点击退出按钮
            logOut(){
                this.isShow = true;
            },
            //再等等
            await(){
                this.isShow = false;
            },

            //退出登录删除user
            quit(){
                localStorage.removeItem('user');
                this.isShow =false
                //退出登录后去我的页面
                this.$router.push({
                    path:'/profile'
                })
            },
            //去我的页面
            goMy() {
                this.$router.push({
                    path:'/profile'
                })
            },
            phone(){
                this.phoneIsShow = true;
            },
            phoneClose(){
                this.phoneIsShow = false;
            }
        }
    }
</script>

